import React from "react";
import Card from "react-bootstrap/card";
import styles from "./card.module.css";
import Col from "react-bootstrap/col";

import projects from "./ProjectList";

function Projects() {
  return projects.map((project) => (
    <Col className="col-auto">
      <Card className={styles.cardSize}>
        <Card.Img variant="top" src={project.image} />
        <Card.Body>
          <Card.Title>{project.title}</Card.Title>
          <p className={styles.techSub}>{project.tech}</p>
          <Card.Text>{project.desc}</Card.Text>
          <Card.Text className="text-center">
            {project.hasPage && (
              <a href={project.pageLink} className={styles.iconDesc}>
                <span className={styles.hiddenSpan}>Read More</span>
              </a>
            )}
            {project.hasFigma && (
              <a href={project.figmaLink} className={styles.iconFigma}>
                <span className={styles.hiddenSpan}>View Figma</span>
              </a>
            )}
            {project.hasGH && (
              <a href={project.ghLink} className={styles.iconGitHub}>
                <span className={styles.hiddenSpan}>View GitHub</span>
              </a>
            )}
            {project.hasSiteLink && (
              <a href={project.siteLink} className={styles.iconLink}>
                <span className={styles.hiddenSpan}>View Site</span>
              </a>
            )}
          </Card.Text>
        </Card.Body>
      </Card>
    </Col>
  ));
}

export default Projects;
