import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import './App.css';
import HomePage from './pages/HomePage/HomePage';
import ProjectPage from './pages/ProjectPage/ProjectPage';
import ToDoListPage from './pages/Projects/ToDoListPage';
import QuizRedesignPage from './pages/Projects/QuizRedesignPage';
import FreelancePage from "./pages/Projects/FreelancePage";
import CraigslistPage from "./pages/Projects/CraigslistPage";
import VenmoPage from "./pages/Projects/VenmoPage";
import SchoolPage from './pages/SchoolPage/SchoolPage';
import ExperiencePage from "./pages/ExperiencePage/ExperiencePage";
import ASA from "./pages/ASA/ASA";
import NorCalUVSA from "./pages/NorCalUVSA/NCUVSA";
import Jade from "./pages/Jade/Jade";
import SJSU from "./pages/SJSU/SJSU";
import R1 from "./pages/R1/R1";
import NotFoundPage from "./pages/404/NotFoundPage";


function App() {
  return (
    // Routes for website
    <Router>
      <Switch>
        <Route exact path="/">
          <HomePage />
        </Route>
        <Route exact path="/projects">
          <ProjectPage />
        </Route>
        <Route exact path="/projects/what-to-do">
          <ToDoListPage />
        </Route>
        <Route exact path="/projects/quiz-redesign">
          <QuizRedesignPage />
        </Route>
        <Route exact path="/projects/freelance-1">
          <FreelancePage />
        </Route>
        <Route exact path="/projects/craigslist-case-study">
          <CraigslistPage />
        </Route>
        <Route exact path="/projects/venmo-case-study">
          <VenmoPage />
        </Route>
        <Route exact path="/school">
          <SchoolPage />
        </Route>
        <Route exact path="/experience">
          <ExperiencePage />
        </Route>
        <Route exact path="/asa">
          <ASA />
        </Route>
        <Route exact path="/norcaluvsa">
          <NorCalUVSA />
        </Route>
        <Route exact path="/jade">
          <Jade />
        </Route>
        <Route exact path="/sjsu">
          <SJSU />
        </Route>
        <Route exact path="/r1rcm">
          <R1 />
        </Route>
        {/*keep at very end*/}
        <Route path="*" component={NotFoundPage} />
      </Switch>
    </Router>
  );
}

export default App;
