import React from "react";
import styles from "./exp.module.css";

import experiences from "./ExperienceList";

function Experiences() {
  return experiences.map((exp) => (
    <React.Fragment>
      <a className={styles.jobLink} href={exp.jobLink}>
        <div className={styles.section}>
          <span className={styles.col1}>
            <h2 className={styles.job}>{exp.jobTitle}</h2>
            {exp.hasjobTitle2 && (
              <h2 className={styles.job}>{exp.jobTitle2}</h2>
            )}
            <h2>{exp.company}</h2>
          </span>
          <span className={styles.col2}>
            <img src={exp.image} alt={exp.company} />
          </span>
        </div>
      </a>
      {exp.hasBreak && <hr />}
    </React.Fragment>
  ));
}

export default Experiences;
